@import "./color.less";

.title {
	margin-bottom: 20px;
}

.section {
	// margin-bottom: 30px;
}

.colorContainer {
	margin: 20px 0;
}

.commItem {
	width: 200px;
	height: 120px;
	padding: 20px;
	color: #fff;
	font-size: 13px;
	margin-right: 10px;
	margin-bottom: 10px;
	border-radius: 2px;
	box-sizing: border-box;
}

.mainColor {
	background: @main-color;
}

.mainHoverColor {
	background: @main-hover-color;
}

.mainClickColor {
	background: @main-press-color;
}

.assistColor {
	background: @assist-color;
}
.textColor {
	background-color: @text-color;
}

.textLinkColor {
	background: @text-link-color;
}

.textImportantColor {
	background: @text-important-color;
}
.textSubImportantColor {
	background: @text-sub-important-color;
}

.textTipColor {
	background: @text-tip-color;
}

.textSubTipColor {
	background: @text-sub-tip-color;
}

.borderColor {
	background: @border-color;
	color: #0f0f0f;
}

.areaDividerColor {
	background: @divider-area-color;
	color: #0f0f0f;
}

.backColor {
	background: @background-color;
	color: #0f0f0f;
}

.backDarkColor {
	background: @background-dark-color;
	color: #0f0f0f;
}

.backHoverColor {
	background: @background-hover-color;
	color: #0f0f0f;
}

.backActiveColor {
	background: @background-active-color;
	color: #0f0f0f;
}
.backSelectedColor {
	background: @background-select-color;
	color: #0f0f0f;
}

.warningColor {
	background: @warning-color;
}

.errorColor {
	background: @error-color;
}
